<template>
  <div class="login-container">
    <el-form autoComplete="off" :model="loginForm" :rules="loginRules" ref="loginForm" label-position="left" label-width="0px"
      class="card-box login-form">
      <h3 class="title">平台登录</h3>
      <el-form-item prop="phone">
        <span class="svg-container"><icon-svg icon-class="xinrenzhinan"></icon-svg></span>
        <el-input name="phone" type="text" v-model="loginForm.phone" autoComplete="off" placeholder="手机号码"></el-input>
      </el-form-item>
      <el-form-item prop="password">
        <span class="svg-container"><icon-svg icon-class="mima"></icon-svg></span>
        <el-input name="password" type="password" v-model="loginForm.password" autoComplete="off" placeholder="密码"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" style="width:100%;" :loading="loading" @click.native.prevent="handleLogin">
          登录
        </el-button>
      </el-form-item>
      <div class="tips">
            <router-link to="/regist" class="fr">没有账号？立即注册</router-link>
      </div>
    </el-form>

  </div>
</template>

<script>
  import { validatePhone,validatePass } from 'utils/validate';

  export default {
    components: {  },
    name: 'login',
    data() {
      const isPhone = (rule, value, callback) => {
        if (!validatePhone(value)) {
          callback(new Error('请输入正确的合法手机号码'));
        } else {
          callback();
        }
      };
      const isPass = (rule, value, callback) => {
        if (!validatePass(value)) {
          callback(new Error('密码格式不正确'));
        } else {
          callback();
        }
      };
      return {
        loginForm: {
          /*phone: '15922947551',
          password: 'admin'*/
          phone: '',
          password: ''
        },
        loginRules: {
          phone: [
                { required: true, trigger: 'blur', validator: isPhone }
          ],
          password: [
                { required: true, trigger: 'blur', validator: isPass }
          ]
        },
        loading: false
      }
    },
    methods: {
      handleLogin() {
        this.$refs.loginForm.validate(valid => {
          if (valid) {  
              this.loading = true;
              this.$store.dispatch('Login', this.loginForm).then((json) => {
              this.loading = false;
              if(json.code=="0000"){
                this.$router.push({ path: '/' });
              }else{
                this.$message(json.message);
              }
              //this.showDialog = true;
            }).catch(err => {
              this.$message.error(err);
              this.loading = false;
            });
          } else {
            console.log('error submit!!');
            this.loading = false;
            return false;
          }
        });
      }
    }
  }
</script>

<style rel="stylesheet/scss" lang="scss" >
  @import "src/styles/mixin.scss";
  .tips {
    font-size: 14px;
    color: #fff;
    margin-bottom: 5px;
  }

  .login-container {
    @include relative;
    height: 100vh;
    background-color: #2d3a4b;
    input:-webkit-autofill {
      -webkit-box-shadow: 0 0 0px 1000px #293444 inset !important;
      -webkit-text-fill-color: #fff !important;
    }
    input {
      background: transparent;
      border: 0px;
      -webkit-appearance: none;
      border-radius: 0px;
      padding: 12px 5px 12px 15px;
      color: #eeeeee;
      height: 47px;
    }
    .el-input {
      display: inline-block;
      height: 47px;
      width: 85%;
    }
    .svg-container {
      padding: 6px 5px 6px 15px;
      color: #889aa4;
    }
    .title {
      font-size: 26px;
      font-weight: 400;
      color: #eeeeee;
      margin: 0px auto 40px auto;
      text-align: center;
      font-weight: bold;
    }
    .login-form {
      position: absolute;
      left: 0;
      right: 0;
      width: 400px;
      padding: 35px 35px 15px 35px;
      margin: 120px auto;
    }
    .el-form-item {
      border: 1px solid rgba(255, 255, 255, 0.1);
      background: rgba(0, 0, 0, 0.1);
      border-radius: 5px;
      color: #454545;
    }
    .forget-pwd {
      color: #fff;
    }
  }
</style>



